.game {
  display: flex;
  flex-direction: row;
  height: min-content;
}

.board_row:after {
  clear: both;
  content: '';
  display: table;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.game_info {
  margin-left: 20px;
  overflow: hidden;
  overflow-y: scroll;
  height: 100;
}

.status {
  margin-bottom: 10px;
}

